.profile__info {
  display: grid;
  grid-template-columns: minmax(min-content, max-content) 24px 1fr;
  align-items: center;
  gap: 16px 18px;
}

@media screen and (max-width: 550px) {
  .profile__info {
    grid-template-columns: minmax(min-content, max-content) 18px;
    gap: 10px 10px;
  }
}